.title {
  padding: 8px;
  margin-top: 0;
  border-radius: 8px;
  color: rgb(255, 255, 255);
  font-size: 26px;
  font-weight: 800;
  text-align: center;
  background-image: repeating-linear-gradient(
    -45deg,
    #afb6e2,
    #afb5da 15px,
    #c2caf0 15px,
    #c1c9f7 30px
  );
}
